// TODO Remember user entered values (like length) between runs
// TODO Hover over buttons to show tooltip
// TODO Should we gray out skipDelay text if delay is 0?
// TODO Don't try to send email if environment variable is not present
// TODO DRY up duplicate strings like "Show Next Digit"

<template>
  <div class='calculator' onselectstart='return false'>
    <div class='display gridcolumn1slant4'>{{displayDigits}}</div>
    <div @click='appendToDisplay(1)' class='button'>1</div>
    <div @click='appendToDisplay(2)' class='button'>2</div>
    <div @click='appendToDisplay(3)' class='button'>3</div>
    <div @click='appendToDisplay(4)' class='button'>4</div>
    <div @click='appendToDisplay(5)' class='button'>5</div>
    <div @click='appendToDisplay(6)' class='button'>6</div>
    <div @click='appendToDisplay(7)' class='button'>7</div>
    <div @click='appendToDisplay(8)' class='button'>8</div>
    <div @click='appendToDisplay(9)' class='button'>9</div>
    <div @click='appendToDisplay(0)' class='button gridcolumn1slant3 button-bottom'>0</div>
    <div @click='start' class='button darker button-bottom'>Start</div>
    <div class='button-lighter'>{{lengthMessage}}</div>
    <div @click='incrementLength' class='button-lighter'>+</div>
    <div @click='decrementLength' class='button-lighter'>-</div>
    <div class='button-lighter'>{{delayMessage}}</div>
    <div @click='incrementDelay' class='button-lighter'>+</div>
    <div @click='decrementDelay' class='button-lighter'>-</div>
    <div @click='skipDelay' class='button-lighter'>Skip Delay</div>
    <div @click='showHideNextDigit' class='button-lighter gridcolumn2slant4'>{{showHideMessage}}</div>
    <div class='button-lighter gridcolumn1slant3'>{{forwardOrReverse}}</div>
    <div @click='switchForwardAndReverse' class='button-lighter'>Switch</div>
  </div>
</template>

<script>

// TODO why does WebStorm/ESLint say 'Howl' is not defined ?
// import Howl from 'howler';
// const {Howl} = require('howler');

export default {
  // components: {
  //   'howler': Howl
  // },
  data() {
    return {
      previous: null,
      displayDigits: '',
      saveDisplayDigits: null,
      operator: null,
      operatorClicked: false,
      sequence: [],
      sequenceLength: 12,  // set to 12 for production
      index: -1,          // current index into sequence
      lengthMessage: 'Length 12',   // set to 12 for production TODO use sequenceLength in this message when I figure out how to run code at startup
      digitDelay: 0,
      delayMessage: 'Delay 0',
      showHideMessage: 'Show Next Digit',
      forwardOrReverse: 'Forward',
      userCanClickStart: true,
      userCanClickDigits: false,
      userCanSwitchDirection: true,
      timerEvent: null,
      // TODO store sounds in .mp3 files after I figure out how to do this with Howl
      sounds: [
        'data:audio/mp3;base64,',
        'data:audio/mp3;base64,',
        'data:audio/mp3;base64,',
        'data:audio/mp3;base64,',
        'data:audio/mp3;base64,',
        'data:audio/mp3;base64,',
        'data:audio/mp3;base64,',
        'data:audio/mp3;base64,',
        'data:audio/mp3;base64,',
        'data:audio/mp3;base64,',
      ],
      ok_sound: 'data:audio/mp3;base64,',
    };
  },
  methods: {
    switchForwardAndReverse() {
      if (this.userCanSwitchDirection) {
        if (this.forwardOrReverse === 'Forward') {
          this.forwardOrReverse = 'Reverse';
        } else {
          this.forwardOrReverse = 'Forward';
        }
      }
    },
    /**
     * Returns a random integer between min (inclusive) and max (inclusive).
     * The value is no lower than min (or the next integer greater than min
     * if min isn't an integer) and no greater than max (or the next integer
     * lower than max if max isn't an integer).
     * Using Math.round() will give you a non-uniform distribution!
     */
    getRandomInt(min, max) {
      min = Math.ceil(min);
      max = Math.floor(max);
      return Math.floor(Math.random() * (max - min + 1)) + min;
    },
    getSequence() {
      this.sequence = [];
      this.index = -1;
      for (let i = 0; i < this.sequenceLength; i++) {
        this.sequence[i] = this.getRandomInt(0,9);
      }
      // Make sure first and last digits are different, so we can tell if the user is repeating it forwards
      // or backwards
      if (this.sequence[0] === this.sequence[this.sequenceLength-1]) {
        this.sequence[this.sequenceLength-1] = (this.sequence[0] + 1) % 10;
      }
    },
    sayDigits() {
      this.index++;
      if (this.index >= this.sequenceLength) {
        // After we've said all the digits, the user can click a digit or start over
        this.userCanClickDigits = true;
        this.userCanClickStart = true;
        return;
      }
      this.userCanClickDigits = false;
      const sound = new Howl({
        src: [this.sounds[this.sequence[this.index]]],
        onend: this.setDigitTimeout,
      });

      sound.play();
    },
    setDigitTimeout() {
      this.timerEvent = setTimeout(this.sayDigits, this.digitDelay*200)
    },
    clearDisplay() {
      this.displayDigits = '';
    },
    appendToDisplay(number) {
      this.cancelHide();
      if (this.userCanClickDigits) {
        // For now, repeatingForward is always true
        // Save this code for future use
        // if (this.displayDigits.length === 0) {
        //   if (parseInt(number) === this.sequence[0]) {
        //     this.repeatingForward = true;
        //   } else if (parseInt(number) === this.sequence[this.sequenceLength - 1]) {
        //     this.repeatingForward = false;
        //   } else {
        //     this.repeatingForward = null;
        //   }
        // }

        let sequenceIndex;
        let repeatingForward = this.forwardOrReverse === 'Forward';
        if (repeatingForward) {
          sequenceIndex = this.displayDigits.length;
        }
        else {
          sequenceIndex = this.sequenceLength - 1 - this.displayDigits.length;
        }

        if (this.sequence[sequenceIndex] === parseInt(number)) {
          if (repeatingForward) {
            this.displayDigits = this.displayDigits + number;
            if (sequenceIndex === this.sequenceLength-1) {
              this.okSound();
              this.userCanSwitchDirection = true;
            }
          }
          else {
            this.displayDigits = number + this.displayDigits;
            if (sequenceIndex === 0) {
              this.okSound();
              this.userCanSwitchDirection = true;
            }
          }
        }
        if (this.displayDigits.length === 1) {
          // User has just correctly clicked the first digit.  Until now, he's been able to switch direction.
          this.userCanSwitchDirection = false;
        }
      }
    },
    okSound() {
      const sounds_ok = new Howl({
        src: [this.ok_sound],
      });
      sounds_ok.play();
    },
    start() {
      if (this.userCanClickStart) {
        this.userCanClickStart = false;
        this.userCanSwitchDirection = true;
        this.clearDisplay();
        this.getSequence();
        this.sayDigits();
        this.showHideMessage = 'Show Next Digit';
      }
    },
    // TODO Autodecrement/increment when button is held
    incrementLength() {
      this.sequenceLength++;
      this.lengthMessage = `Length ${this.sequenceLength}`
    },
    decrementLength() {
      if (this.sequenceLength > 1) {
        this.sequenceLength--;
        this.lengthMessage = `Length ${this.sequenceLength}`
      }
    },
    // Cancel the delay for the next digit only
    // TODO clicking this button repeatedly says the digits with sounds overlapping, especially at small delays
    skipDelay() {
      if (this.timerEvent != null) {
        clearTimeout(this.timerEvent);
        this.timerEvent = null;
        this.sayDigits();
      }
    },
    incrementDelay() {
      this.digitDelay++;
      this.delayMessage = `Delay ${this.digitDelay}`
    },
    decrementDelay() {
      if (this.digitDelay > 0) {
        this.digitDelay--;
        this.delayMessage = `Delay ${this.digitDelay}`
      }
    },
    showHideNextDigit() {
      if (this.showHideMessage === 'Show Next Digit') {
        this.showHideMessage = 'Hide Next Digit';
        this.saveDisplayDigits = this.displayDigits;
        let correctLength = this.displayDigits.length;
        // TODO Trying to insert 5 spaces just seemed to insert 1, so added periods to make the display length 5
        if (this.forwardOrReverse === 'Forward') {
          this.displayDigits = this.sequence.slice(0, correctLength).join('') + ' . . ' + this.sequence.slice(correctLength, correctLength+1);
        }
        else {
          // TODO Simplify the following line (and maybe the one above, too).
          this.displayDigits = (this.sequence.slice(0, this.sequence.length-correctLength).join('') + ' . . ' + this.sequence.slice(this.sequence.length-correctLength, this.sequence.length).join(''));   // .split("").reverse().join("");
        }
      }
      else {
        this.showHideMessage = 'Show Next Digit';
        this.displayDigits = this.saveDisplayDigits;
        this.saveDisplayDigits = null;
      }
    },
    cancelHide() {
      if (this.showHideMessage === 'Hide Next Digit') {
        this.showHideNextDigit();
      }
    }
  }
};
</script>

<style scoped>
.calculator {
  margin: 0 auto;
  width: 100vw;
  font-size: 2rem;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-auto-rows: minmax(10vh, auto);
  border: 5px solid #111;
  box-shadow: -3px 3px 15px rgba(17,17,17,0.4);
  line-height: 10vh;
}

.display {
  background: #ddd;
  border: 1px solid #111;
  border-top: 0;
  font-size: 2rem;
  cursor: default;
  overflow: hidden;
  text-overflow: ellipsis;
  padding: 0 1rem;
}

.gridcolumn1slant3 {
  grid-column: 1 / 3;
}

.gridcolumn1slant4 {
  grid-column: 1 / 4;
}

.gridcolumn2slant4 {
  grid-column: 2 / 4;
}

.button {
  background: hsl(0, 0%, 80%);
  background: linear-gradient(15deg,hsl(0, 0%, 70%) 0%, hsl(0, 0%, 80%) 100%);
  border: 1px solid #111;
  cursor: pointer;
}

.button-lighter {
  background: #ddd;
  border: 1px solid #111;
  cursor: pointer;
}

.button:active {
  outline:none;
  border: 1px solid #000000BF;
  /*noinspection CssRedundantUnit*/
  box-shadow: inset 0px 0px 5px #00000080;
  /*noinspection CssRedundantUnit*/
  -moz-box-shadow: inset 0px 0px 5px #00000080;
  /*noinspection CssRedundantUnit*/
  -webkit-box-shadow: inset 0px 0px 5px #00000080;
}

.button-bottom {
  border-bottom: 0;
}

.darker {
  background: hsl(0, 0%, 70%);
  background: linear-gradient(15deg, hsl(0, 0%, 60%) 0%, hsl(0, 0%, 70%) 100%);
}

@media only screen and (min-width: 768px) {
  .calculator {
    width: 20vw;
    /* The following two lines are inherited from the kepad-only source I based this on.
       Don't know why they're required in this case only. */
    grid-auto-rows: minmax(7.5vh, auto);
    line-height: 7.5vh;
  }
}

@media only screen and (min-width: 1024px) {
  .calculator {
    width: 30vw;
  }
}

/* Why does this one seem to be used for 1920x1080 and 1920x1200 displays?  I expected the following
   1600px one to be used. */
@media only screen and (min-width: 1280px) {
  .calculator {
    width: 35vw;
  }
}

@media only screen and (min-width: 1600px) {
  .calculator {
    width: 45vw;
  }
}
</style>
